<template>
    <div class="back">
        <!-- 头部返回 start -->
        <header class="m-head">
            <div class="head-select-layout">
                <a href="javascript:;" @click="goback" class="goto-prev">
                    <span class="iconfont icon-fanhui"></span>
                    返回
                </a>
                <h4 class="head-title">选包中心</h4>
            </div>
        </header>
        <!-- 头部返回 end -->        
    </div>
</template>
<script>
export default {
    methods: {
        goback() {
            this.$router.go(-1)
        }
    }
}
</script>
<style lang="stylus" scoped>
.back
    width: 100%
    position: fixed
    z-index: 999
    header 
        width: 100%
        height: 0.9333rem
        background-color:#C7154A
        .head-select-layout
            position: relative
            max-width: 17.0667rem
            margin: 0 auto
            .goto-prev
                color: #fff
                position: absolute
                font-size: 0.3733rem
                height: 0.9333rem
                line-height: 0.9333rem
                top: 0
                left: 0.1333rem
            .icon-fanhui
                font-weight: bold 
            .head-title
                text-align: center
                display: block
                margin: 0 auto
                font-weight: 700
                font-size: 0.3733rem
                width: 100%
                line-height: 0.9333rem
                color: #fff
       
</style>





