<template>
  <div class="list">
    <vback></vback>
    <vfilter></vfilter>
    <div class="product-list clearfix">
      <div class="product col-m-6 col-l-4" v-for="product in productList" :key="product.id">
        <router-link :to="'/detail?id='+product.id+'&type=1'">
          <div class="pic">
            <img :src="product.goods.imgUrl" alt="">
          </div>
          <div class="info">
            <p class="name">[{{product.title}}]{{product.goods.name}}}</p>
            <p class="price">￥{{product.goods.newprice}}
              <span>{{product.goods.oldprice}}</span>
            </p>
          </div>
        </router-link>

      </div>

    </div>
    <div class="pagination">
      <button class="prev" @click="prev">上一页</button>
      <span>{{currentPage}}/{{totalPage}}</span>
      <button class="next" @click="next">下一页</button>
    </div>
    <vfooter></vfooter>
  </div>
</template>
<script>
import vback from '../back/Back'
import vfilter from '../filter/Filter'
import vfooter from '../footer/Footer'

export default {
  components: {
    vfooter,
    vback,
    vfilter
  },
  data() {
    return {
      productList: [],
      currentPage: 1,
      totalPage: "",
      params:{},
      keyword:'',
      category:''
    }
  },
  mounted(){
    if(this.$route.query.key){
      this.$axios.get('/search', { params: { key: this.$route.query.key} }).then((res) => {
        this.productList = res.data.result
        console.log(this.productList)
      })
    }else{
      this.keyword = this.$route.query.key
      this.category = this.$route.query.category
      this.params = {
        keyword: this.keyword,
        category: this.category,
        currentPage: this.currentPage
      }
      this.getList(this.params)
    }    
  },
  methods: {
    getList(p){
      this.$axios.get('/productList', { params: p}).then((res) => {
        this.productList = res.data.result
        this.totalPage = res.data.totalPage
      })
    },
    prev() {
      if (this.currentPage == 1) {
        return
      }
      this.currentPage = --this.currentPage
      this.getProduct(this.currentPage)
    },
    next() {
      if (this.currentPage == this.totalPage) {
        return
      }
      this.currentPage = ++this.currentPage
      this.getProduct(this.currentPage)
    },
    getProduct(index) {
      var category = this.$route.query.category
      this.$axios.get('/productList', { params: { category: category, currentPage: index } }).then((res) => {
        this.productList = res.data.result
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
.list
  .product-list
    .product
      display: block
      padding: 0.1333rem
      box-sizing: border-box
      a
        color: #333
      .pic
        width: 100%
        img 
          width: 100%
      .info
        text-align: center
        padding: 0.1333rem 0
        .name
          height: 0.6933rem
          font-size: 0.3733rem
          white-space: nowrap
          text-overflow: ellipsis
          overflow: hidden
        .price
          color: #d41c4e
          font-size: 0.4267rem
          span
            color: #999
            text-decoration: line-through
            font-size: 0.32rem
  .pagination
    padding-bottom: 1.7067rem
    padding-top: 0.6rem
    text-align: center
    button
      width: 1.8667rem
      height: 0.8rem
      font-size: 0.4267rem
      font-weight: 600
      background-color: #eee
      border: none
    span 
      display: inline-block
      width: 1.8667rem
      height: 0.8rem
      font-size: 0.3733rem
      font-weight: 500
</style>



