<template>
    <div class="footer">
        <!-- 底部导航 start -->
        <nav>
            <ul>
                <li>
                    <router-link to='/'>
                        <i class="iconfont icon-shouye"></i>
                    </router-link>
                </li>
                <li @click="cart">
                    <router-link to='/cart'>
                        <i class="iconfont icon-cart"></i>
                    </router-link>

                </li>
                <li @click.prevent="user">
                    <router-link to='/user'>
                        <i class="iconfont icon-yonghu"></i>
                    </router-link>
                </li>
            </ul>
            <span v-text="num"></span>
        </nav>
        <!-- 底部导航 end -->
        <router-view />
    </div>
</template>
<script>
export default {
    methods: {
        user() {
            if (localStorage.username) {
                this.$router.push('/user')
            } else {
                this.$router.push('/login')
            }
        },
        cart() {
            if(localStorage.username){
                this.$router.push('/cart')
            }else{
                this.$router.push('/login')                
            }
        }
    },
    computed: {
        num() {
            var sum = 0
            this.$axios.get('/cart').then((res)=>{
                var cartList = res.data.result    
                if(cartList){
                    cartList.forEach(function(item){
                        sum += item.number
                    })
                }
                this.$store.commit('total',sum)
            })
        return this.$store.state.count
        }
    }
}
</script>
<style lang="stylus" scoped>
nav
    width:100%
    position: fixed
    bottom: 0
    ul
        display: flex
        flex-direction: row
        height: 64.9988px 
        text-align: center
        line-height: 64.9988px
        li
            flex: 1
            background-color: #f5f5f5
            position: relative
            a
                display: block
                color: #999
                &.router-link-exact-active
                    color: #d41c4e
                i 
                    font-size: 0.7467rem 
    span 
        position:absolute
        display: inline-block
        top: 12px
        left: 50%
        width: 15px
        height: 15px
        background-color: #d41c4e
        border: 1px solid #d41c4e
        border-radius: 10px
        text-align: center
        color: #fff
</style>




