<template>
    <div class="user">
      <vback></vback>
	    <section class="big-tank">
	        <div class="member-top">
	            <div class="member">
	                <div class="member-pic">
	                	<img id="head appear" onerror="javascript:this.src='http://mbbimg.mbaobao.com/static/h5web/img/user_head.jpg';" src="http://mbbimg.mbaobao.com/static/h5web/img/user_head.jpg" alt="15568559998" width="18%">
	                </div>
	                <dl class="myuser">
	                    <dt>名称：<em id="username">{{username}}</em></dt>
	                    <dd>麦豆：<em id="usermaidou">100</em>点</dd>
	                </dl>
	            </div>
	        </div>
	    </section>
	    <section class="main-mymbb modTop">
	        <ul>
	            <li class="order"><a href="#"><div class="iconfont icon icon-mulu"></div>我的订单</a></li>
	        </ul>
	         <ul>  
	            <li class="coupon"><a href="#"><div class="iconfont icon icon-youhuiquan"></div>优惠券</a></li>
	            <li class="MaiDou"><a href="#"><div class="iconfont icon icon-m"></div>我的麦豆</a></li>
	            <li class="collect"><a href="#"><div class="iconfont icon icon-icon_user-defined"></div>我的收藏</a></li>
	        </ul>
	        <ul> 
	            <li class="adress"><a href="#"><div class="iconfont icon icon-biji"></div>地址管理</a></li>
	        </ul>
	        <div class="login-out" @click="loginout"><a href="#" class="modBtnWhite modBtnWidth">退出登录</a></div>
	    </section>
      
	</div>
</template>
<script>
import vback from '../back/Back'
import vfooter from '../footer/Footer'
export default {
  components:{
      vfooter,
      vback
  },
  data(){
    return{
      username:""
    }
  },
  created(){
    if(localStorage.username){
      this.username=localStorage.username
    }
  },
  methods:{
    loginout(){
      localStorage.clear()
      this.$router.push('/login')
    }
  }
}
</script>
<style lang="stylus" scoped>
    @import url("../../assets/stylus/iconfont.styl");
	.user {
    width:100%;
    font-size: 0.32rem;   
    margin:0 auto;
    background-color:#ededed;
    padding-bottom: 0.2667rem;
    }
	.big-tank{
	  background-color:white;
    padding:0.2667rem;
    padding-top: 0.9333rem;
    margin-bottom:0.2667rem;
    }
    .member{
    width: 95%;
    margin: 0.4rem auto;
    overflow: hidden;
    }
    .member img {
    float: left;
    display:block;
    border: 0;
    vertical-align: bottom;
    }
    .myuser {
    float: left;
    margin: 0  0  0.32rem 0.2667rem;
    text-align: left;
    width: 60%;
    }
    .myuser dt {
    display:block;
    height: 0.6667rem;
    line-height: 0.6667rem;
    background: #d9e4f2;
    color: #2f5d96;
    padding: 0 0.8rem 0 0.5333rem;
    margin-bottom: 0.1333rem;
    overflow: hidden;
    }
    .myuser dd {
    height: 0.5333rem;
    line-height: 0.5333rem;
    display: block;
    margin-left: 0;
    padding: 0  0.8rem  0  0.5333rem;
    border-bottom: 1px dotted #d6d6d6;
    }
    .main-mymbb ul {
    padding: 0;
    margin-bottom: 0.2667rem;
    background-color: #fff;
    }
    .main-mymbb li {
    list-style:none;
    height:1.0667rem;
    padding-left: 3%;
    line-height:1.0667rem;
    border-bottom:0.0267rem solid #d1d1d1;
    background:url(http://mbbimg.mbaobao.com/static/h5web//mod/page/mbbMobile/1.0.2/img/25.png) no-repeat center right;
    background-size:0.6133rem 0.4rem;
    }
    .main-mymbb a {
    color: #333;
    }
	.icon {
    float: left;
    width: 0.5867rem;
    height: 0.5867rem;
    background-color:#f96e95;
    font-size: 0.3733rem;
    line-height: 0.5867rem;
    text-align: center;
    border-radius: 0.08rem;
    color: #fff;
    margin-right: 0.1333rem;
    margin-top: 0.24rem;
    } 
    .login-out {
    width:70%;
    height: 0.9333rem;
    background-color: #c6204d;
    margin: 0 auto;
    line-height:0.9333rem;
    border-radius:0.1333rem;
    }
    .login-out a {
    display: block;
    width: 100%;
    height: 0.9333rem;
    color: #fff;
    text-align: center;
    } 
    .icon-youhuiquan{
      background-color:#d781b2;
    } 
    .icon-m{
      background-color:#90ae4e;
    }  
    .icon-icon_user-defined{
      background-color:#ff576b;
    }   
    .icon-biji{
      background-color :#ae9ae1;
    }      
</style>





