<template>
  <div class="m-container">
    <vhead></vhead>
    <div class="login">
      <form action="#" id="form-login" method="post">
        <div id="web-login">
          <!-- 用户名、密码 -->
          <ul class="g-login">
            <li id="g-u">
              <input type="text" id="loginName" class="input-style" name="loginName" placeholder="账  号:" v-model="username">
            </li>
            <li id="g-p">
              <input type="password" id="password" name="password" class="input-style" placeholder="密  码:" v-model="password">
            </li>
          </ul>
          <div class="error-tips">
            <div id="err-msg" class="error-tips-content" v-if="show">用户名或密码不正确</div>
          </div>
          <div class="go-mbb">
            <input type="button" class="g-b-login" value="登   录" @click.prevent="checkuser">
            <a href="#" class="b-b-forgetpw">忘记密码？</a>
          </div>
        </div>
      </form>
      <div class="go-other">
        <h4>其他账号登录</h4>
      </div>
      <div class="other-icons">
        <a href="#">
          <img src="../../../static/images/login/ico_alipay.png" alt="支付宝">
        </a>
        <a href="#">
          <img src="../../../static/images/login/ico_qq.png" alt="QQ">
        </a>
        <a href="#">
          <img src="../../../static/images/login/ico_weibo.png" alt="微博">
        </a>
      </div>
      <div class="go-register">
        没有账号？
        <router-link to="/register">立即注册</router-link>
      </div>
    </div>
    <vfooter></vfooter>
  </div>
</template>
<script>
import vhead from '../header/Header'
import vfooter from '../footer/Footer'
import { mapGetters, mapActions } from 'vuex'
export default {
  components:{
    vhead,
    vfooter
  },
  data() {
    return {
      username: "",
      password: "",
      show: false
    }
  },
  methods: {
    checkuser() {
      var ERR_OK = 0
      this.$axios.post('/checkuser', { username: this.username, password: this.password }).then((res) => {
        if (res.data.errno == ERR_OK) {
          console.log('登录成功')
          localStorage.username=this.username
          this.show = false
          this.$router.push('/')
        } else {
          console.log('登录失败')
          this.show = true
        }
      })
    }
  }
};
</script>
<style>
.m-container {
  width: 100%;
  margin: 0 auto;
  background: #fff;
}

.login {
  width: 100%;
  margin: 0 auto;
  color: #666;
  box-sizing: border-box;
  padding: 0.2667rem;
}

#form-login {
  height: 6.9333rem;
}

.web-login {
  width: 100%;
  height: 6.9333rem;
  background-color: yellow;
}

.g-login {
  width: 100%;
  height: 2.9333rem;
  list-style: none;
  padding-top: 1.0667rem;
}

.g-login li {
  margin: 0 auto;
  width: 6.9333rem;
  height: 1.0667rem;
}

#g-p {
  padding-top: 0.5333rem;
}

.input-style {
  width: 6.9333rem;
  height: 1.0667rem;
  border-radius: 0.1333rem;
  font-size: 0.4533rem;
  color: #000;
  padding-left: 0.2667rem;
  border: 0.0267rem solid #888;
}

.error-tips {
  width: 6.9333rem;
  height: z;
  margin: 0 auto;
  line-height: 0.6667rem;
  color: #c6204d;
}

.error-tips-content {
  font-size: 0.4rem;
}

.go-mbb {
  width: 7.3333rem;
  height: 2.1333rem;
  margin: 0 auto;
  padding-left: 0.1333rem;
}

.g-b-login {
  margin: 0.2667rem auto;
  display: block;
  width: 7.2267rem;
  height: 1.2rem;
  border: none;
  border-radius: 0.1333rem;
  color: #fff;
  font-size: 0.5333rem;
  background-color: #c6204d;
}

.b-b-forgetpw {
  float: right;
  height: 0.6667rem;
  font-size: 0.4rem;
  line-height: 0.6667rem;
  color: #555;
  text-decoration: underline;
}

.go-other {
  width: 100%;
  padding: 0.1333rem 0;
}

.go-other h4 {
  letter-spacing: 0.0267rem;
  font-size: 0.3733rem;
  font-weight: 400;
}

.other-icons {
  width: 100%;
  padding-bottom: 0.6667rem;
}

.other-icons a {
  margin: 0 0.24rem;
}

.other-icons img {
  width: 1.1733rem;
  height: 1.12rem;
}

.go-register {
  width: 100%;
  font-size: 0.4267rem;
  padding-bottom: 0.8rem;
}

.go-register a {
  color: #c6204d;
  text-decoration: underline;
}
</style>



