<template>
    <div class="m-container">
        <vhead></vhead>
        <div class="register">
            <div class="tab-selected">
                <a href="#">会员注册</a>
            </div>
            <div class="reg-body">
                <form action="#" id="form-register" method="post">
                    <ul class="go-login">
                        <li>
                            <input type="text" id="mobile" class="input-style" placeholder="手机号：" v-model="mobile" v-on:blur="checkMobile">
                            <div class="box" v-if="mobileBox">
                                <div class="err" v-if="!mobileCheck">手机号码格式不正确</div>
                            </div>
                        </li>
                        <li>
                            <input type="password" class="input-style" name="password" placeholder="输入密码:" v-model="pwd" v-on:blur="checkPwd">
                            <div class="box" v-if="pwdBox">
                                <div class="err" v-if="!pwdCheck">最少6位，包括大小写字母，数字，特殊符号</div>
                            </div>
                        </li>
                        <li>
                            <input type="password" class="input-style" name="confirmPassword" placeholder="确认密码:" v-model="repwd" v-on:blur="checkRepwd">
                            <div class="box">
                                <div class="box" v-if="repwdBox">
                                    <div class="err" v-if="!repwdCheck">两次密码不一致</div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <input type="text" id="input1" class="input-style" placeholder="验证码：" v-model="code">
                            <input type="button" class="input-code" value="发送验证码" @click.prevent="verify" ref="code" :disabled="sendCode">
                        </li>
                    </ul>
                    <div class="go-register">
                        <input type="button" value="注  册" class="g-b-register" @click.prevent="register" :disabled="!checkAll">
                    </div>
                </form>
            </div>
        </div>
        <vfooter></vfooter>
    </div>
</template>
<script>
import vhead from '../header/Header'
import vfooter from '../footer/Footer'
export default {
    data() {
        return {
            mobile: "",
            pwd: "",
            repwd: "",
            code: "",
            mobileBox: false,
            pwdBox: false,
            repwdBox: false,
            content: '',
            tNum: "T150606060606",
            sendCode: false,
            itfCode: null
        }
    },
    components: {
        vhead,
        vfooter
    },
    methods: {
        checkMobile() {
            //根据正则验证的结果来判定是否需要将提示框显示
            if (!this.mobileCheck) {
                this.mobileBox = true
            } else {
                this.$axios.get('/checkmobile', { params: { mobile: this.mobile } }).then((res) => {
                    alert(res.data.msg)
                })
            }
        },
        checkPwd() {
            if (!this.pwdCheck) {
                this.pwdBox = true
            }
        },
        checkRepwd() {
            if (!this.repwdCheck) {
                this.repwdBox = true
            }
        },
        register() {
            this.$axios.post('/register', { mobile: this.mobile, password: this.pwd }).then((res) => {
                if (res.data.errno == 0) {
                    alert(res.data.msg)
                    this.$router.push('/login')
                }
            })
        },
        verify() {
            var time = 20
            //生成6位随机数验证码
            this.itfCode = Math.floor(Math.random() * 1000000)
            console.log(this.mobile)
            this.content = `{ "name": "张三疯", "code": ${this.itfCode}, "minute": "5" }`
            var _this = this
            var timer = setInterval(count, 1000)
            function count() {
                if (time > 0) {
                    time--
                    _this.$refs.code.value = `剩余 ${time} s`
                    _this.sendCode = true
                } else {
                    clearInterval(timer)
                    _this.$refs.code.value = `点击重新发送`
                    _this.sendCode = false
                }
            }
            //发送ajax请求调用短信接口
            this.$axios({
                method: "get",
                headers: { "Authorization": "APPCODE ad4f0eabc30544b28f8d1f99f07350fc" },
                url: "http://ali-sms.showapi.com/sendSms?" + "content=" + this.content + "&mobile=" + this.mobile + "&tNum=" + this.tNum,
            }).then((res) => {
                console.log(res)
            })
        }
    },
    computed: {
        mobileCheck() {
            //正则验证 返回布尔值
            var reg = /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9])|(17[0-9]))\d{8}$/
            if(reg.test(this.mobile)){
                this.sendCode = false
            }
            return reg.test(this.mobile)
        },
        pwdCheck() {
            var reg = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/
            return reg.test(this.pwd)
        },
        repwdCheck() {
            if (this.pwd === this.repwd) {
                return true
            }
            return false
        },
        codeCheck() {
            if (this.code == this.itfCode) {
                return true
            }
            return false
        },
        checkAll() {
            return this.mobileCheck && this.pwdCheck && this.repwdCheck && this.codeCheck
        }
    }
};
</script>
<style>
.register {
    width: 100%;
}

.tab-selected {
    width: 100%;
    height: 1.3333rem;
    margin: 0 auto;
    margin-top: 1.28rem;
}

.tab-selected a {
    display: block;
    color: #cb2b5a;
    font-weight: bold;
    font-size: 0.4267rem;
    text-align: center;
    line-height: 1.3333rem;
}

.reg-body {
    width: 100%;
    margin: 0 auto;
    padding-top: 0.4rem;
    color: #666;
}

.go-login {
    width: 6.9333rem;
    margin: 0 auto;
    padding: 0.2667rem 0 0.1333rem;
    list-style: none;
}

.input-code {
    width: 2.6667rem;
    height: 0.95rem;
    background-color: #eee;
    border: none;
    margin: 0.4rem 0 0 0.1rem;
    border-radius: 0.08rem;
    border: 0.0267rem solid #888;
    color: #888;
}

.input-style {
    width: 100%;
    height: 0.9333rem;
    color: #000;
    padding-left: 0.2667rem;
    margin-top: 0.4rem;
    font-size: 0.4267rem;
    border: 0.0267rem solid #888;
    border-radius: 0.1333rem;
}

#input1 {
    width: 3.7333rem;
}

.go-login a {
    display: inline-block;
    width: 1.1467rem;
    height: 0.9333rem;
    cursor: pointer;

    color: rgb(176, 14, 0);
}

.go-login h4 {
    line-height: 0.9333rem;
    font-weight: 400;
}

.go-register {
    width: 6.9333rem;
    margin: 0 auto;
    margin-top: 1.6rem;
}

.g-b-register {
    background: #c6204d;
    color: #fff;
    width: 100%;
    height: 1.0667rem;
    margin: 0.1333rem 0;
    font-size: 0.48rem;
    line-height: 1.0667rem;
    text-align: center;
    border-radius: 0.1333rem;
    border: none;
}

.err {
    color: #c6204d;
    font-size: 0.4rem;
    padding-top: 0.1333rem;
}
</style>



