<template>
    <div class="detail">
        <vheader></vheader>
        <div class="product-container" v-if="product">
            <div class="detail-head">
                <span @click="back">
                    <i class="iconfont icon-fanhui1"></i>
                </span>
                <span>[{{product.title}}] {{product.goods.name}}</span>
                <router-link to="/">
                    <i class="iconfont icon-shouye"></i>
                </router-link>
            </div>
            <!-- 页面刚进入的时候imgs是一个空数组 无法渲染 在imgs请求完成之后才能使用v-if -->
            <div v-for="item in infos" :key="item.id">
                <!-- v-if根据tab传入的type值元素的type值比较,来确定显示哪个轮播图 -->
                <vswiper :img="item.imgs" v-if="item.type==type"></vswiper>
            </div>
            <div class="price">
                <span class="newprice">￥{{product.goods.newprice}}</span>
                <div class="oldprice">
                    <div class="tag">
                        <span class="discount">优惠价</span>
                    </div>
                    <s>￥{{product.goods.oldprice}}</s>
                </div>
            </div>
            <div class="pic">
                <span v-for="item in infos" :key="item.id">
                    <!-- tab传入商品相应的type 根据type值空值上面轮播图的显示 -->
                    <img :src="item.coverImg" alt="图片分类" @click="tab(item.type)" :class="{active:isActive=item.type==type?true:false}">
                </span>
            </div>
        </div>
        <div class="tabs">
            <div class="panels-tab">
                <router-link to="/detail/product">
                    <span>图文详情</span>
                </router-link>
                <router-link to="/detail/introduction">
                    <i class="iconfont icon-caogao"></i>
                    <span>参数说明</span>
                </router-link>
                <router-link to="/detail/ratings">
                    <i class="iconfont icon-pingjia"></i>
                    <span>评价</span>
                </router-link>
            </div>
            <router-view/>
        </div>
        <vbuy></vbuy>
    </div>
</template>
<script>
import vheader from '../header/Header'
import vswiper from '../swiper/Swiper'
import vintroduction from './Introduction'
import vproduct from './Product'
import vratings from './Ratings'
import vbuy from '../buy/Buy'

export default {
    components: {
        vheader,
        vswiper,
        vbuy
    },
    data() {
        return {
            show: true,
            imgs: [],
            coverImg: '',
            infos: [],
            type: "",
            product: null
        }
    },
    methods: {
        tab(type) {
            //将数据库获取的type值赋值给data中的type值，用于swiper的if判定
            this.type = type
            //将type传值入store,tab栏切换地址栏使用,参数用于购物车
            let id = this.$route.query.id
            let params = { id: id, type: type }
            this.$router.push(`/detail?id=${params.id}&type=${params.type}`)
        },
        back() {
            this.$router.go(-1)
        }
    },
    created() {
        var id = this.$route.query.id
        var type = this.$route.query.type
        this.$axios.get('/product', { params: { id: id } }).then((res) => {
            this.product = res.data.result
        })
        this.$axios.get('/detail', { params: { id: id } }).then((res) => {
            this.infos = res.data.result.infos
            this.type = type
        })
    },
    updated() {
        // 获取商品id和type          
        let id = this.$route.query.id
        let params = { id: id, type: this.type }
        //将商品的id和type存入store  
        this.$store.commit('getParams', params)
    }
}
</script>
<style lang="stylus" scoped>
@import url("../../assets/stylus/iconfont.styl");
@import "../../../static/swiper.min.css";
.detail
    width: 100%
    .product-container
        .detail-head
            width: 100%
            height: 0.9333rem
            line-height: 0.9333rem
            box-sizing: border-box
            padding: 0.9333rem 0.2667rem
            box-shadow: 0 2px 2px rgba(50,50,50,.2)
            display: flex
            flex-direction: row
            justify-content: space-between
            a
                color: #666
                display: block
            span 
                color: #666
                display: block
                text-align: center
        .price
            text-align: center
            padding: 0.2667rem
            display: flex
            flex-direction: row
            justify-content: center
            .newprice
                color: #d41c4e
                font-size: 0.96rem
                font-weight: 600
                margin-right: 0.1333rem
            .oldprice
                color: #666
                font-size: 0.32rem
                .tag
                    border: 1px solid #ef135b
                    padding: 0.08rem
                    border-radius: 0.0533rem
                    color: red
        .pic
            text-align: center
            padding-bottom: 0.2667rem
            border-bottom: 1px dotted #e5e5e5
            img 
                width: 1.0667rem
                height: 1.0667rem
                &.active
                    border: 2px solid #d41c4e
                &:first-child
                    margin-right: 0.2667rem
    .tabs
        .panels-tab 
            display:flex
            flex-direction: row
            top:0rem
            width: 100%
            margin: 0rem auto
            height: 0.9rem
            overflow: hidden
            z-index :200
            background-color: #e4e4e4
            a
                flex: 1;
                height: 0.9rem
                text-align: center
                line-height: 0.9rem
                border-right: 0.0267rem solid #d2d2d2
                border-top: 0.0267rem solid #d2d2d2
                border-bottom: 0.0267rem solid #d2d2d2
                float :left
                color: #333
                display: block
                &:last-child
	                border-right: none
            .router-link-exact-active
                    border-bottom: 0.0267rem solid #fff
                    background-color: #fff
                    i 
                        color: #d41c4e

</style>







