<template>
    <div class="buys">
        <router-link to="/cart">
            <div class="myser">
                <i class="iconfont icon icon-cart"></i>
                <span v-text="num"></span>
            </div>
        </router-link>
        <button class="cart" @click="add">加入购物车</button>
        <button class="buy" @click="buyNow">立即购买</button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            cartObj: {}
        }
    },
    methods: {
        add() {
            var params = this.$store.state.params
            this.$axios.get('/product', { params: { id: params.id } }).then((res) => {
                let result = res.data.result
                this.cartObj = {
                    id: params.id,
                    title: result.title,
                    name: result.goods.name,
                    img: result.goods.imgUrl,
                    price: result.goods.newprice,
                    type: params.type,
                    number: 1 //应当换为查询数+1
                }
                this.$axios.post('/cart', this.cartObj).then((res) => {
                    //添加入购物车成功之后 更改当前的购物车商品数量
                    if (res) {
                        var sum = 0
                        this.$axios.get('/cart').then((res) => {
                            var cartList = res.data.result
                            if (cartList) {
                                cartList.forEach(function(item) {
                                    sum += item.number
                                })
                            }
                            this.$store.commit('total', sum)
                        })
                    }
                })
            })
            alert('加入购物车成功')
        },
        buyNow() {
            alert(11)
        }
    },
    computed: {
        num() {
            return this.$store.state.count
        }
    }
}
</script>
<style lang="stylus" scoped>
@import url("../../assets/stylus/iconfont.styl");
.buys
    width:100%
    text-align:center
    position: fixed
    bottom: 0
    z-index: 999
    background-color: #fff
    padding: 0.2667rem 0
    display: flex
    flex-direction: row
    .myser
        flex: 1
        display: inline-block
        padding-top:0.1333rem
        width :2.4rem
        height 1.12rem
        .icon-cart
            font-size: 0.8rem
            color: #e1074a
            position: relative
        span 
            position: absolute
            top:0.3467rem
            left: 1.4rem
            width: 0.4rem
            height: 0.4rem
            background-color: #e1074a
            color: #ffffff
            text-align: center
            line-height: 0.4rem
            border-radius: 0.2667rem
    a
        padding:0
        vertical-align:top
        text-decoration: none
        color: #333
    .customer 
        height :1.3333rem
        padding: 0 0.2667rem
        color: #e1074a;
        text-align: center
        font-size: 0.32rem
        .icon
            float: left 
            width :2.4rem
            height :0.72rem
            margin:0 auto    
            font-size:0.5867rem
            color:#e1074a
        span
            width :1.6rem
            height :0.72rem
            text-align :center
            font-size:0.3733rem
    .cart
        width:3.4667rem
        background-color: #fff 
        color: #c7154a 
        box-sizing:border-box
        border: 0.0267rem solid #c7154a
        margin: 0.1333rem 0 0
        
    .buy
    .cart
        flex: 1
        display :inline-block
        height:1.12rem
        line-height: 1.12rem
        border-radius: 0.5333rem
        text-align: center;
        font-size: 0.48rem
        outline: none
    .buy
         width:2.6667rem 
         background-color: #e1074a
         color: #fff 
         margin:0.1333rem 0.2667rem 0  
         border: none    
</style>



