<template>
    <!-- 商品评价 -->
    <div class="ratings">
        <div class="content">
            <div class="posts-main">
                <div class="goods">
                    <strong class="feng">0.0%</strong>
                    <span>好评率</span>
                </div>
                <div class="goodsitem">
                    <ul>
                        <li>好评
                            <span>0.0%</span>
                        </li>
                        <li>中评
                            <span>0.0%</span>
                        </li>
                        <li>差评
                            <span>0.0%</span>
                        </li>
                    </ul>
                </div>
                <div class="posts-btn">
                    <a href="#" class="btn">我要评价</a>
                </div>
            </div>
            <div class="posts-bottom"></div>
        </div>
        <pfooter></pfooter>
    </div>
</template>
<script>
import pfooter from './Pfooter'
export default {
    components:{
        pfooter
    }
}
</script>
<style >
.content {
    padding: 0.5333rem 0;
    border-bottom: 0.0267rem solid #ddd;
}

.goods {
    width: 32%;
    display: inline-block;
    text-align: center;
}

.feng {
    font-size: 0.48rem;
    display: block;
    color: #c7154a;
}

.feng span {
    color: #555;
    font-size: 0.4267rem;
    font-weight: 700;
}

.goodsitem {
    width: 32%;
    display: inline-block;
}

.goodsitem li {
    display: list-item;
    vertical-align: baseline;
    color: #999;
}

.posts-btn {
    width: 30%;
    display: inline-block;
    height: 0.6667rem;
    line-height: 0.6667rem;
    text-align: center;
    background-color: #c7154a;
    border-radius: 0.2667rem;
    vertical-align: middle;
    margin-bottom: 0.2133rem;
}

.posts-btn a {
    color: #fff;
}

.posts-bottom {
    padding: 0.2667rem 0px 0.2667rem 0px;
    width: 100%;
    border-bottom: 0.0267rem solid #ccc;
}
</style>


