<template>
  <div class="main">
    <vheader></vheader>
    <div class="search">
      <div class="search-key-form">
        <form action="">
          <input type="text" placeholder="输入关键词或商品编号" v-model="key">
          <router-link :to="'/list?key='+key">
            <i class="iconfont icon-sousuo"></i>
          </router-link>
        </form>
      </div>
    </div>
    <!-- 轮播图 start -->
    <div class="swiper">
      <swiper :options="swiperOption" ref="mySwiper">
        <!-- 这部分放你要渲染的那些内容 -->
        <swiper-slide v-for="banner in banners" :key="banner.id">
          <img :src="banner.src" alt="">
        </swiper-slide>
        <!-- 这是轮播的小圆点 -->
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <!-- 轮播图 end -->
    <!-- tab栏 start -->
    <div class="tab">
      <ul>
        <li>
          <router-link to="/new" class="channel-item">
            <i class="iconfont icon-xinpin"></i>
            <span>新品</span>
          </router-link>
        </li>
        <li>
          <a href="#" class="channel-item">
            <i class="iconfont icon-nvbao"></i>
            <span>女士真皮</span>
          </a>
        </li>
        <li>
          <a href="#" class="channel-item">
            <i class="iconfont icon-biaoqian"></i>
            <span>爆销热款</span>
          </a>
        </li>
        <li>
          <a href="#" class="channel-item">
            <i class="iconfont icon-mulu"></i>
            <span>选包</span>
          </a>
        </li>
      </ul>
    </div>
    <!-- tab栏 end -->
    <!-- 商品栏 start -->
    <div class="product">
      <section class="feature" v-for="item in advList" :key="item.id">
        <a href="javascript:;" class="feat-tit">
          <p>{{item.description}}</p>
        </a>
        <a class="feature-left">
          <router-link :to="'/list?category='+item.category">
            <img :src="item.coverImg" alt="">
          </router-link>
        </a>

        <div class="feature-right">
          <div class="goods" v-for="advInfo in item.advInfos" :key="advInfo.id">
            <a href="#" class="product-container clearfix">
              <div class="description">
                <span>{{advInfo.productName}}</span>
              </div>
              <a class="product-img">
                <img :src="advInfo.advImg" alt="">
              </a>
            </a>
          </div>
        </div>
      </section>
    </div>
    <!-- 商品栏 end -->
    <!-- 热销风云榜 start -->
    <section class="leaderboard">
      <h4>热卖品牌</h4>
      <div class="hotList">
        <div class="hotgoods-container">
          <a href="#"><img src="/static/images/leader/1.jpg" alt=""></a>
          <a href="#"><img src="/static/images/leader/2.jpg" alt=""></a>
          <a href="#"><img src="/static/images/leader/3.jpg" alt=""></a>
        </div>
        <div class="hotgoods-container">
          <a href="#"><img src="/static/images/leader/4.jpg" alt=""></a>
          <a href="#"><img src="/static/images/leader/5.jpg" alt=""></a>
          <a href="#"><img src="/static/images/leader/6.jpg" alt=""></a>

        </div>
      </div>
    </section>
    <!-- 热销风云榜 end -->
    <vfooter></vfooter>
  </div>
</template>
<script> 
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import vheader from '../header/Header'
import vfooter from '../footer/Footer'

export default {
  components: {
    swiper,
    swiperSlide,
    vheader,
    vfooter
  },
  data() {
    return {
      banners: [{ src: '/static/images/banner/banner01.jpg' }, { src: '/static/images/banner/banner02.jpg' }, { src: '/static/images/banner/banner03.jpg' }, { src: '/static/images/banner/banner04.jpg' }, { src: '/static/images/banner/banner05.jpg' }],
      swiperOption: {
        //是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true 
        notNextTick: true,
        pagination: '.swiper-pagination',
        slidesPerView: 'auto',
        centeredSlides: true,
        autoplayDisableOnInteraction: false,
        autoplay: 2000,
        loop: true,
        paginationClickable: true,
        onSlideChangeEnd: swiper => {
          //这个位置放swiper的回调方法 
          this.page = swiper.realIndex + 1
          this.index = swiper.realIndex
        }
      },
      advList: [],
      key:""
    }
  },
  //定义这个sweiper对象 
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {
    //这边就可以使用swiper这个对象去使用swiper官网中的那些方法 
    this.swiper.slideTo(0, 0, false);
    this.$axios.get('/advList').then((res)=>{
      this.advList = res.data.result
    })
  }
} 
</script> 

<style lang="stylus" scoped>
@import "../../../static/swiper.min.css";
@import url("../../assets/stylus/iconfont.styl");
.main
  max-height: 17.0667rem
  .search
    width: 100%
    margin-top: 1rem
    .search-key-form
      width: 95%
      height: 0.8533rem
      margin: 0 auto
      padding: 0.2133rem 0
      form
        position: relative 
        width: 100%
        input 
          box-sizing: border-box
          padding: 0 0.2133rem        
          font-size: 0.38rem
          width: 100%
          height: 0.8rem
          outline: none
          border: 1px solid #969696
          border-radius: 0.1333rem
        a
          position: absolute
          display:  inline-block
          top: 0.08rem
          right: 1%
          color: #969696
          line-height: 0.65rem
          i 
            font-size: 0.7rem
  .swiper
    width: 100%
    img 
      width: 100%
    .swiper-pagination-bullet-active
      background-color: #d41c4e
  .tab
    width: 100%
    height: 2.56rem
    ul
      padding-top: 0.5867rem
      display: flex
      flex-direction: row
      li
        list-style: none
        flex: 1
        height: 1.9733rem
        text-align: center
        a
          display: block
          text-align: center
          color: #d41c4e
          i
            display:block
            font-size: 0.7467rem
            width: 1.2267rem
            height: 1.2267rem
            border: 0.0267rem solid #d41c4e
            border-radius: 0.64rem
            line-height: 1.2267rem
            margin: 0 auto
          span 
            font-size: 0.4rem
            color: #000
            display: block
            padding: 0.1333rem 0
  .product
    .feature
      position: relative
      border-top: 1px solid #e0e0e0
      border-bottom: 1px solid #e0e0e0
      height: 4rem
      overflow: hidden
      margin-bottom: 0.5333rem
      &:after
        content:''
        display: block
        clear: both
      .feat-tit
        display: block
        width: 2.6667rem
        height: 2.6667rem
        position: absolute
        top: 50%
        left: -1.3333rem
        transform: translateY(-50%)
        border: 2px solid #cccece
        background-color: #fff
        border-radius: 1.3333rem
        p
          font-size: 0.3733rem
          font-weight: 700
          color: #3c3c3c
          width: 0.5333rem
          padding:0.5333rem 0 0 1.5rem
          letter-spacing: 0.0533rem
      .feature-left
        width: 58%
        float: left
        img
          width: 100%
      .feature-right
        width: 41%
        float:right
        display: flex
        flex-direction: column
        align-content: space-between
        .goods
          flex: 1
          display: flex
          flex-direction: row
          justify-content: space-between
          &:first-child
            border-bottom: 1px dotted #e0e0e0
          .product-container
            display: block 
            color: #666
            .description
              float: left
              span
                line-height: 1.3333rem
                margin-left: 0.5333rem
                font-size: 0.32rem
            .product-img
              float: left
              display: block
              width: 2rem
              height: 2rem
              img 
                width: 100%          
  .leaderboard
    margin-bottom: 1.7333rem   
    h4
      text-align: center
      background-color: #f5f5f5
      height: 1.0667rem
      line-height: 1.0667rem
      font-weight: 700
    .hotList  
      .hotgoods-container
        width: 100%
        display: flex
        flex-direction: row
        justify-content: space-around
        a
          display: block
          flex: 1
          width: 33.3%
          img
            width: 100%

</style>






