<template>
    <section class="carts">
        <vheader></vheader>
        <section class="head">
            <h3 class="title">购物车</h3>
        </section>
        <div class="cart-container">
            <section class="empty-cart" v-if="cartList.length==0">
                <div class="empty">
                    <ul>
                        <li class="pic"><img src="http://mbbimg.mbaobao.com/static/h5web/img/cart.png"></li>
                        <li class="car"> 您的购物车还是空的哦 </li>
                    </ul>
                </div>
            </section>
            <section>
                <div v-for="(item,index) in cartList" class="have-sale" style="display:block;">
                    <ul>
                        <li class="c-item">
                            <div class="pic-2">
                                <a href="#" data-sku="89516">
                                    <!-- < alt="[mbaobao]双面料系列 手提单肩斜挎包" class="goods-pic" width="80" height="80"> -->
                                    <img :src="item.img">
                                </a>
                            </div>
                            <div class="info-wrap">
                                <!-- [mbaobao]双面料系列 手提单肩斜挎包 -->
                                <div class="title">[{{item.title}}]{{item.name}}</div>
                                <div class="gwc clearfix">
                                    <button @click="reduce(index)" class="decrease"></button>
                                    <!-- <input onblur="blurEvent(this)" class="num j_qty_nubmer" id="quantity" autocomplete="off" min="1" max="5" data-id="4494228" data-max="96" value="1" data-price="229" onchange="numChange(this)"> -->
                                    <!-- <input class="" value="1"> -->
                                    <span>{{item.number}}</span>
                                    <button @click="add(index)" class="increase"></button>
                                    <!-- <span>1</span>
                                                            <span>{{item.selected}}</span> -->
                                </div>
                                <div class="price">
                                    ￥{{item.price*item.number}}
                                </div>
                                <span class="remove-btn" @click="del(index)">
                                    <div class="iconfont icon icon-cha"></div>
                                </span>
                            </div>
                        </li>
                    </ul>
                </div>
                <footer class="pay-item">
                    <div class="totalcount">
                        <ul class="price-item">
                            <li>小计金额：
                                <span class="total">{{totalMoney}}</span>
                            </li>
                            <li>应付金额：
                                <span class="c-count"></span> +
                                <span>0.00</span>(运费) =
                                <span class="c-total">{{totalMoney}}</span>
                            </li>
                        </ul>
                        <router-link to="/total">
                            <button class="btm">结算</button>
                        </router-link>
                    </div>
                </footer>
            </section>
        </div>
        <vfooter></vfooter>
    </section>
</template>
<script>
import vheader from '../header/Header'
import vfooter from '../footer/Footer'
export default {
    components: {
        vfooter,
        vheader
    },
    data() {
        return {
            cartList: []
        }
    },
    methods: {
        del(index) {
            var id = this.cartList[index].id
            this.$axios.post('/delete', { id: id }).then((res) => {
                console.log(res.data)
                this.totalNum()
            })
            this.cartList.splice(index, 1)
        },
        reduce(index) {
            if (this.cartList[index].number <= 1) return
            this.cartList[index].number--
            this.totalNum()
            this.opera(index)
        },
        add(index) {
            this.cartList[index].number++
            this.totalNum()
            //获取改变后的数据
            this.opera(index)
        },
        totalNum() {
            if (this.cartList) {
                let sum = 0
                //计算购物车中商品数量
                this.cartList.forEach(function(item) {
                    sum += item.number
                })
                //将数量保存至store
                this.$store.commit('total', sum)
            }
        },
        opera(index) {
            //操作商品增减
            var params = this.$store.state.params
            var number = this.cartList[index].number
            var id = this.cartList[index].id
            this.$axios.post('/cart', { id: id, number: number }).then((res) => {
                console.log(res.data)
            })
        }
    },
    computed: {
        totalMoney() {
            if (this.cartList) {
                let sum = 0
                this.cartList.forEach(function(item) {
                    sum += item.number * item.price
                })
                this.$store.commit('totalMoney',sum)
                return sum.toFixed(2)
            }
        }
    },
    created() {
        this.$axios.get('/cart').then((res) => {
            this.cartList = res.data.result
            this.totalNum()
        })
    }
}

</script>
<style lang="stylus" scoped>
@import url("../../assets/stylus/iconfont.styl");
.head 
    position: relative
    height:0.5333rem
    line-height:0.5333rem
    color: #666
    text-align: center
    padding: 1.2rem 0 0.4rem 0   
    border-bottom: 0.0267rem solid #ddd
.empty-cart
    .car 
        height: 0.5333rem
        font-size:0.4rem
        text-align: center
        line-height: 0.5333rem
        margin-top: 0.2667rem
        color:#bfbfbf
    img 
        width:20%
        margin: 0 auto
        vertical-align:bottom
        display: table-cell
.c-item 
    width: 100%
    clear: both
    display: table
    border-bottom: 0.0267rem solid #ddd
    padding: 0.4rem 0
    .pic-2 
        text-align: center
        position: relative
        vertical-align: middle
        display: table-cell
        width:28%
        img 
            width: 2.1333rem
            height: 2.1333rem
            vertical-align:middle
            margin: 0 auto
    .info-wrap 
        padding: 0.1333rem 0
        padding-right: 1.0667rem
        position: relative
        .title 
            width: 6.4rem
            height: 0.5867rem
            line-height: 0.5867rem
            word-wrap: break-word
            word-break:break-all
            overflow: hidden
            text-overflow: ellipsis
            white-space: nowrap
        .gwc 
            line-height:0.64rem
            height: 0.64rem
            width: 2.9333rem
            button 
                background: #f2f2f2
                border: 0.0267rem solid #e4e4e4
                font-size:0.5333rem
                height: 0.64rem
                width: 0.8rem
                position: relative
                overflow: hidden
                vertical-align: top
                float: left
            span 
                width:0.8rem
                height:0.5867rem
                line-height:0.56rem
                border:1px solid #b1b1b1
                vertical-align:top
                text-align:center
                color:#333
                font-size:0.3733rem
                float:left
            .decrease,
            .increase
                &:before
                    content:''
                    width:0.3467rem
                    height:0.08rem
                    background-color:#888
                    display:block
                    position:absolute
                    left: 30%
                    top: 45%
            .increase
                &:after 
                    content:''
                    width: 0.08rem
                    height: 0.3467rem
                    background-color: #888
                    display: block
                    position: absolute
                    left:48%
                    top:25%
        .price 
            width :65px
            height:0.8rem
            line-height:0.8rem
            text-align:left
            font-size:14px
            cursor:pointer
            color: #d31b4d
        .remove-btn 
            display: inline-block
            width: 0.64rem
            height: 0.64rem
            padding: 0.2667rem
            cursor: pointer
            position: absolute
            top: 20%
            right: 2%
            .icon 
                float: left
                width: 0.5867rem
                height: 0.5867rem
                line-height: 0.5867rem
                text-align: center
            .icon-cha 
                font-size:0.6667rem          
.pay-item 
    padding-bottom: 0.2667rem
    .price-item 
        margin: 0.2667rem 0 0.2667rem 0.2667rem
        display:inline-block
        text-align: left
        li
            height: 0.5867rem
            line-height: 0.5867rem
        span
            font-size:0.3733rem
       .c-total 
            font-size: 0.64rem
            color: #d31d50
            font-weight: bolder
    button
        border: none 
        float: right
        margin-right:0.2667rem
        margin-top:0.5333rem
    .btm 
        font-size: 0.4267rem
        padding:0.32rem 0.5867rem
        text-align: center
        background-color: #d41c50
        height: 1.0667rem
        color: #fff
        border-radius:0.08rem
        line-height:0.5333rem
        display: block
        cursor: pointer
        margin: 0.4rem 0.4rem
</style>



