import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const COUNT = "COUNT"
export default new Vuex.Store({
    state:{
        count:0,
        params:{},
        cartObj:{},
        money:0
    },
    mutations:{
        total(state,sum){
            state.count = sum
        },
        getParams(state,params){
            state.params = params
        },
        cartObj(state,params){
            state.cartObj = params
        },
        totalMoney(state,params){
            state.money = params
        }
    }
})


